<template>
  <div class="card p-3 bg-white mt-3">
    <div class="card-header d-flex ai-center pb-2">
      <i class="iconfont"
         :class="`icon-${icon}`"></i>
      <div class="fs-xl ml-2 flex-grow-1">{{title}}</div>
      <i class="iconfont icon-menu"></i>
    </div>
    <div class="card-banner pb-2 mb-3">
      <slot name="banner" />
    </div>
    <div class="card-body">
      <div class="nav jc-between">
        <div class="nav-item"
             :class="{active: active === index}"
             v-for="(category,index) in categories"
             :key="index"
             @click="tabClik(index)">
          <div class="nav-link">{{category.name}}</div>
        </div>
      </div>
      <div class="pt-2">
        <swiper ref="mySwiper"
                @slide-change="() => active = $refs.mySwiper.$swiper.realIndex"
                :options="{autoHeight: true}">
          <swiper-slide v-for="(category,index) in categories"
                        :key="index">
            <slot name="items"
                  :category="category"></slot>
          </swiper-slide>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    icon: { type: String, required: true },
    title: { type: String, required: true },
    categories: { type: Array, required: true }
  },
  data () {
    return {
      active: 0
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.$swiper
    }
  },
  methods: {
    tabClik (index) {
      this.active = index
      if (!this.swiper.slideTo) {
        this.initSwiper()
      }
      this.swiper.slideTo(index, 300, false)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/scss/_variables.scss';
.card {
  border-bottom: 1px solid $border-color;
  .card-banner {
    border-bottom: 1px solid $border-color;
  }
}
</style>
