<template>
  <div class="page-article">
    <div class="d-flex py-3 px-2 ai-center border-b">
      <i class="iconfont icon-arrow-right text-title"
         @click="$router.go(-1)"></i>
      <strong class="flex-grow-1 text-title fs-lg text-ellipsis">{{article.title}}</strong>
      <span class="fs-sm text-grey">2020-07-10 </span>
    </div>
    <div v-if="article.body">
      <div v-html="article.body"
           class="px-3 article-body">
      </div>
      <div class="px-2 d-flex ai-center mt-4">
        <i class="iconfont icon-Menu pr-2 fs-lg"></i>
        <strong class="text-info fs-lg">相关资讯</strong>
      </div>
      <div v-for="(item,index) in article.related"
           :key="index"
           class="d-flex jc-between px-3 py-1"
           @click="$router.push(`/article/${item._id}`)">
        <span class="flex-grow-1 text-ellipsis">{{item.title}}</span>
        <span>2020-7-12</span>
      </div>
    </div>
    <div v-else>
      <h3>暂无内容</h3>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: String
  },
  mounted () {
    this.fetchArticle()
  },
  data () {
    return {
      article: {}
    }
  },
  methods: {
    async fetchArticle () {
      const res = await this.$http.get(`/article/${this.id}`)
      console.log(res)
      this.article = res.data.article
    }
  }
}
</script>

<style lang="scss">
.page-article {
  .article-body {
    p > img {
      width: 100% !important;
      height: auto;
    }
    iframe {
      width: 100%;
      height: auto;
    }
  }
}
</style>
