<template>
  <div class="hero-page">
    <div class="topbar bg-black py-2 px-3 d-flex ai-center">
      <img src="../assets/image//logo.png"
           height="30"
           @click="$router.push('/')">
      <div class="px-3 flex-grow-1">
        <span class="text-white pr-3">王者荣耀</span>
        <span class="text-white fs-md">攻略战</span>
      </div>
      <div class="text-light">
        <span class="pr-2">更多英雄</span>
        <strong class="fs-xl">&gt;</strong>
      </div>
    </div>
    <div class="top"
         :style="{'background-image':`url(${HeroInfo.banner})`}">
      <div class="info text-white p-3 d-flex flex-column jc-end h-100">
        <div>{{HeroInfo.title}}</div>
        <h2>{{HeroInfo.name}}</h2>
        <div>{{categoryList}}</div>
        <div class="scores d-flex ai-center jc-between"
             v-if="HeroInfo.scores">
          <div class="d-flex ai-center">
            <span>难度</span>
            <i class="psprite"
               :class="[`difficult-${HeroInfo.scores.difficult}`]"></i>
            <span>技能</span>
            <i class="psprite"
               :class="[`skill-${HeroInfo.scores.skill}`]"></i>
            <span>攻击</span>
            <i class="psprite"
               :class="[`attack-${HeroInfo.scores.attack}`]"></i>
            <span>生存</span>
            <i class="psprite"
               :class="[`survive-${HeroInfo.scores.survive}`]"></i>
          </div>
          <div class="pifu text-grey">
            皮肤：8 &gt;
          </div>
        </div>
      </div>
    </div>
    <!-- end of top -->
    <div>
      <div class="bg-white px-3">
        <div class="nav d-flex pt-3 pb-2 jc-around border-b">
          <div class="nav-item"
               v-for="(item,index) in ['英雄初始','进阶攻略']"
               :key="index"
               :class="{active:active===index }"
               @click="tabClik(index)">
            <div class="nav-link">{{item}}</div>
          </div>
        </div>
      </div>
      <swiper :options="{autoHeight:true}"
              ref="mySwiper"
              @slide-change="() => active = $refs.mySwiper.$swiper.realIndex">
        <swiper-slide>
          <div>
            <div class="p-3 bg-white border-b">
              <div class="d-flex">
                <router-link tag="button"
                             to="/"
                             class="btn btn-lg flex-grow-1 ml-2">
                  <i class="iconfont icon-Menu text-primary mr-2"></i>
                  英雄介绍视频
                </router-link>
                <router-link tag="button"
                             to="/"
                             class="btn btn-lg flex-grow-1 ml-2">
                  <i class="iconfont icon-news text-primary mr-2"></i>
                  一图识英雄
                </router-link>
              </div>
              <!-- skills -->
              <div class="skills bg-white mt-4">
                <div class="d-flex jc-around">
                  <img :src="skill.icon"
                       v-for="(skill,index) in HeroInfo.skills"
                       :key="index"
                       width="65px"
                       class="icon"
                       :class="{active: currentSkillIndex === index}"
                       @click="currentSkillIndex = index">
                </div>
                <div v-if="currentSkill">
                  <div class="d-flex ai-center mt-3">
                    <h3 class="m-0 mr-4 fs-lg">{{currentSkill.skillName}}</h3>
                    <span class="fs-sm text-grey-1">(冷却值：{{currentSkill.delay}} 消耗：{{currentSkill.cost}}})</span>
                  </div>
                  <div class="mt-4">
                    {{currentSkill.description}}
                  </div>
                </div>
              </div>
            </div>
            <!-- 出装推荐 -->
            <div class="p-2 mt-3 bg-white border-b">
              <div class="d-flex ai-center mb-3">
                <i class="iconfont icon-Menu"></i>
                <h2 class="fs-xl ml-2">出装推荐</h2>
              </div>
              <span class="fs-xl">顺风出装</span>
              <div class="d-flex flex-wrap jc-between my-3s border-b py-3">
                <div v-for="item in HeroInfo.items1"
                     :key="item._id"
                     class="d-flex flex-column ai-center">
                  <img :src="item.icon"
                       class="item-img">
                  <span class="fs-sm">{{item.name}}</span>
                </div>
              </div>
              <span class="fs-xl">逆风出装</span>
              <div class="d-flex flex-wrap jc-between my-3s  py-3">
                <div v-for="item in HeroInfo.items2"
                     :key="item._id"
                     class="d-flex flex-column ai-center">
                  <img :src="item.icon"
                       class="item-img">
                  <span class="fs-sm">{{item.name}}</span>
                </div>
              </div>
            </div>
            <!-- 使用技巧 -->
            <div class="p-2 mt-3 bg-white border-b">
              <div class="d-flex ai-center mb-3">
                <i class="iconfont icon-Menu"></i>
                <h2 class="fs-xl ml-2">使用技巧</h2>
              </div>
              <div>
                {{HeroInfo.usageTips}}
              </div>
            </div>
            <div class="p-2 mt-3 bg-white border-b">
              <div class="d-flex ai-center mb-3">
                <i class="iconfont icon-Menu"></i>
                <h2 class="fs-xl ml-2">对抗技巧</h2>
              </div>
              <div>
                {{HeroInfo.battleTips}}
              </div>
            </div>
            <div class="p-2 mt-3 bg-white border-b">
              <div class="d-flex ai-center mb-3">
                <i class="iconfont icon-Menu"></i>
                <h2 class="fs-xl ml-2">团队思路</h2>
              </div>
              <div>
                {{HeroInfo.teamTips}}
              </div>
            </div>
            <!-- 英雄关系 -->
            <div class="p-2 mt-3 bg-white border-b">
              <div class="d-flex ai-center mb-3">
                <i class="iconfont icon-Menu"></i>
                <h2 class="fs-xl ml-2">最佳搭档</h2>
              </div>
              <div v-for="(item,index) in HeroInfo.partners"
                   :key="index"
                   class="d-flex mb-3">
                <img :src="item.hero.avatar"
                     width="48px"
                     height="48px"
                     class="mr-3">
                <span>
                  {{item.description}}
                </span>
              </div>
            </div>
          </div>
        </swiper-slide>
        <swiper-slide>
          <div v-for="b in 10"
               :key="b"
               class="d-flex p-2 border-b bg-white">
            <img src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1463344495,3718771746&fm=26&gp=0.jpg"
                 alt=""
                 width="116px"
                 height="64.5px">
            <div class="d-flex flex-column jc-between flex-grow-1">
              <span>【快速上手英雄技能】赵云</span>
              <div class="d-flex">
                <i class="iconfont icon-video"></i>
                <span class="flex-grow-1">233.3万</span>
                <span>2020-7-12</span>
              </div>
            </div>
          </div>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: String
  },
  data () {
    return {
      HeroInfo: {
        categories: [],
        scores: {},
        skills: [],
        partners: []
      },
      currentSkillIndex: 0,
      active: 0
    }
  },
  mounted () {
    this.fetchHeroInfo()
  },
  computed: {
    categoryList () {
      return this.HeroInfo.categories.map(v => v.name).join('/')
    },
    currentSkill () {
      return this.HeroInfo.skills[this.currentSkillIndex]
    }
  },
  methods: {
    async fetchHeroInfo () {
      const res = await this.$http.get(`/heroes/${this.id}`)
      this.HeroInfo = res.data
      console.log(res)
    },
    tabClik (index) {
      this.active = index
      this.$refs.mySwiper.$swiper.slideTo(index, 300, false)
    }
  }
}
</script>

<style lang="scss" >
.hero-page {
  .icon-arrow-right {
    transform: rotate(80deg);
  }
  .top {
    height: 50vw;
    background-size: 100%;
  }
  .skills {
    .icon {
      border: 3px solid transparent;
      border-radius: 50%;
      &.active {
        border: 2px solid #db9e3f;
        border-radius: 50%;
      }
    }
  }
  .item-img {
    width: 46.5px;
    height: 46.5px;
    border-radius: 23px;
  }
}
</style>
