<script>
import dayjs from 'dayjs'
import ListCard from '@/components/ListCard'
export default {
  filters: {
    date (val) {
      return dayjs(val).format('MM/DD')
    }
  },
  data () {
    return {
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination'
        },
        paginationClickable: true,
        slidesPerView: 1,
        loop: true,
        autoplay: {
          delay: 2000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        }
      },
      isClose: false,
      categories: [],
      heroList: []
    }
  },
  mounted () {
    this.fetchNewsLsit()
    this.fetchHeroList()
  },
  methods: {
    closeIcons () {
      this.isClose = !this.isClose
    },
    async fetchNewsLsit () {
      const res = await this.$http.get('/newslist')
      // console.log(res)
      this.categories = res.data.cats
    },
    async fetchHeroList () {
      const res = await this.$http.get('/heroList')
      this.heroList = res.data.heroes
    },
    goArticle (id) {
      this.$router.push(`/article/${id}`)
    }
  },
  components: {
    ListCard
  }
}
</script>
